<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 状态码 -->
            <a-form-model-item
              label="状态码"
              prop="statusCode"
              ref="statusCode"
            >
              <a-input
                v-model="entity.statusCode"
                placeholder="请输入状态码"
                name="statusCode"
                :disabled="!!id"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态名称 -->
            <a-form-model-item
              label="状态名称"
              prop="statusName"
              ref="statusName"
            >
              <a-input
                v-model="entity.statusName"
                placeholder="请输入状态名称"
                name="statusName"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态分类 -->
            <a-form-model-item
              label="状态分类"
              prop="category"
              ref="category"
            >
              <a-select
                v-model="entity.category"
                placeholder="请选择状态分类"
                name="category"
              >
                <a-select-option v-for="item in categoryOptions" :key="item.value" :value="item.value">
                  {{ item.label }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 严重程度 -->
            <a-form-model-item
              label="严重程度"
              prop="severity"
              ref="severity"
            >
              <a-select
                v-model="entity.severity"
                placeholder="请选择严重程度"
                name="severity"
              >
                <a-select-option v-for="item in severityOptions" :key="item.value" :value="item.value">
                  {{ item.label }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status">
                <a-radio value="启用">启用</a-radio>
                <a-radio value="禁用">禁用</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="24">
            <!-- 描述 -->
            <a-form-model-item
              label="描述"
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入描述信息"
                :rows="4"
                :maxLength="1000"
                name="description"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="24">
            <!-- 解决方案 -->
            <a-form-model-item
              label="解决方案"
              prop="solution"
              ref="solution"
            >
              <a-textarea
                v-model="entity.solution"
                placeholder="请输入解决方案"
                :rows="4"
                :maxLength="1000"
                name="solution"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>
      <g-collapse headerName="详细信息" v-if="id">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditResourceStatusCode",
  mixins: [GNdoPageEditBase],
  data() {
    return {
      entity: {
        statusCode: "",
        statusName: "",
        category: undefined,
        severity: undefined,
        status: "启用",
        description: "",
        solution: "",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      },
      // 状态分类选项
      categoryOptions: [
        { value: "HARDWARE", label: "硬件故障" },
        { value: "SOFTWARE", label: "软件故障" },
        { value: "NETWORK", label: "网络故障" },
        { value: "SYSTEM", label: "系统故障" },
        { value: "OTHER", label: "其他" }
      ],
      // 严重程度选项
      severityOptions: [
        { value: "CRITICAL", label: "严重" },
        { value: "MAJOR", label: "重要" },
        { value: "MINOR", label: "次要" },
        { value: "WARNING", label: "警告" },
        { value: "INFO", label: "提示" }
      ],
      rules: {
        statusCode: [
          { required: true, message: "状态码不能为空", trigger: "change" },
          {
            pattern: /^[A-Z][0-9]{3}$/,
            message: "状态码格式不正确，应为1个大写字母后跟3个数字",
            trigger: "change"
          }
        ],
        statusName: [
          { required: true, message: "状态名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            message: "状态名称不能包含前后空格",
            trigger: "change"
          }
        ],
        category: [
          { required: true, message: "状态分类不能为空", trigger: "change" }
        ],
        severity: [
          { required: true, message: "严重程度不能为空", trigger: "change" }
        ],
        status: [
          { required: true, message: "状态不能为空", trigger: "change" }
        ]
      }
    };
  },

  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取状态码数据
        setTimeout(() => {
          this.loading = false;
          this.entity = {
            statusCode: "E001",
            statusName: "设备离线",
            category: "NETWORK",
            severity: "CRITICAL",
            status: "启用",
            description: "设备无法连接到网络",
            solution: "检查网络连接和设备电源",
            createdBy: "admin",
            createdTime: "2024-03-18 10:00:00",
            modifiedBy: "admin",
            modifiedTime: "2024-03-18 11:55"
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },

    resetFormData() {
      this.entity = {
        statusCode: "",
        statusName: "",
        category: undefined,
        severity: undefined,
        status: "启用",
        description: "",
        solution: "",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      };
    },

    async save() {
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.statusName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null
          });
        }, 500);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}
</style> 